body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#F7FFF7;background-color:#555555;margin:0;padding:0;font-family:'Montserrat', sans-serif;line-height:1.5}h1{font-size:4vw}h2{font-size:2.5vw;margin:0}h3{font-size:1.5vw;margin:0}p,li,a{font-size:1vw}.btn{color:#000000;background-color:#ffffff;border:none;border-radius:2px;text-align:center;text-decoration:none;padding:10px 15px;cursor:pointer;-webkit-transition-duration:0.4s;transition-duration:0.4s}.btn:hover{background-color:#4596ff;color:#ffffff}header{background:#343434;color:#F7FFF7;font-family:Arial, Helvetica, sans-serif;margin-bottom:25px}header img{margin:40px 0 0 20px;float:right;height:30vw;border-radius:4px}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.header-wrap{max-width:800px;width:85%;margin:0 auto;padding:60px 0}footer{background-color:#343434;padding:15px 0 15px 25px}footer p{margin:0}footer a{color:#ffffff}footer a:hover{color:#4596ff}.content{background:#2F3061;margin-bottom:25px}.content-head{margin:15px auto;text-align:center}@media only screen and (max-width: 1023px){h1{font-size:10vw}h2{font-size:5.5vw;margin:0}h3{font-size:3.5vw;margin:0}p,li,a{font-size:3vw}.btn:active{background-color:#4596ff;color:#ffffff}header{text-align:center}header img{width:40vw;height:auto;margin:10px auto;float:none}}@media only screen and (max-width: 1023px) and (min-width: 720px){header img{width:25vw}}@media only screen and (max-width: 719px) and (min-width: 320px){header img{width:40vw}}

.content-slide{position:relative;margin:20px}.slideshow-container{position:relative;overflow:hidden;width:100%;height:38vw}.slide-textOverlay{position:absolute;top:0;left:10%;height:100%;width:80%;pointer-events:none}.slide-head{text-align:left;text-shadow:2px 2px #4596ff}.slide-text,.slide-text--skill,.slide-text--desc{position:absolute;right:0;padding:20px;text-shadow:2px 2px #4596ff}.slide-text--skill{bottom:15%;margin:0;text-align:right}.slide-text--skill li{display:inline;margin-left:5px;font-size:2vw}.slide-text--desc{bottom:0;font-size:1.5vw;text-align:right;max-width:50%}.content-nav{margin-top:15px;text-align:center}.content-navOverlay{position:relative}.nav-btn,.nav-btn--next,.nav-btn--prev{position:absolute;margin:0;top:50%;-webkit-filter:invert(99%) sepia(0%) saturate(2845%) hue-rotate(252deg) brightness(110%) contrast(101%);filter:invert(99%) sepia(0%) saturate(2845%) hue-rotate(252deg) brightness(110%) contrast(101%);width:50px;height:auto;cursor:pointer}.nav-btn--next{right:10px}.nav-btn--prev{left:10px;-webkit-transform:rotate(180deg);transform:rotate(180deg)}.nav-btn:hover,.nav-btn--next:hover,.nav-btn--prev:hover{-webkit-filter:invert(63%) sepia(94%) saturate(3680%) hue-rotate(194deg) brightness(101%) contrast(101%);filter:invert(63%) sepia(94%) saturate(3680%) hue-rotate(194deg) brightness(101%) contrast(101%)}.modal-open{position:absolute;bottom:0;left:0;margin:20px;font-size:1vw;width:9vw;pointer-events:initial !important}.transition-left1{-webkit-transition:left 1s ease-in-out;transition:left 1s ease-in-out}.pos-abs{position:absolute}.pos-rel{position:relative}.left-cur{left:0}.left-next{left:100vw}.left-prev{left:-100vw}@media only screen and (max-width: 1023px){.slide-head{margin:10px 0 0 0}.slide-text--skill li{font-size:3vw}.slide-text--desc{font-size:3vw}.slide-text--skill{bottom:22%}.slide-text--desc{max-width:68%}}@media only screen and (max-width: 1023px) and (min-width: 720px){.slideshow-container{height:60vw}.slide-text--desc{line-hei